<template>
  <div class="home height100">
    <v-toolbar class="secondary">
      <v-toolbar-title>
        {{otherUsername}}
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-toolbar-items>
        <v-btn icon @click="call">
          <v-icon>mdi-phone</v-icon>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-container class="px-3 pt-4 mx-0" style="max-height: 100%">
      
      <ChatMsgs style="max-height: 700px" ref="chat-msgs"
    class="overflow-y-auto" :messages="messages"></ChatMsgs>

        
    
      <v-text-field v-model="newMessage" class="">
        <v-btn slot="append" @click="sendMessage">
          <v-icon>mdi-send</v-icon>
        </v-btn>
      </v-text-field>
    
      
    </v-container>
  </div>
</template>

<script>
import ChatMsgs from '../components/ChatMessages'

import MsgServ from '../services/chatService'
export default {
  name: 'Home',

  data: ()=> {
    return {
      otherUsername: null,
      newMessage: null,
      messages: []
    }
  },

  methods: {
    async sendMessage(){
      if(!this.newMessage) return
      this.newMessage = this.newMessage.trim()
      if(this.newMessage === '') return
      

      let payload = {
        content: this.newMessage,
        senderToken: localStorage.getItem('token'),
        reciverUsername: this.otherUsername
      }

      let msg = {
        content: this.newMessage,
        user1: {
          username: this.$store.state.username
        },
        //TODO kako da postavim id? (mogu nakon slanja poruke httpom)
        //cekati i oda je poslati socketom
        id: Math.random() * 10000000,
        createdAt: new Date()
      }
      this.messages.push(msg)
      this.scrollToBottom('chat-msgs')

      this.$socket.client.emit('new-message', {
        username: this.otherUsername,
        msg: msg
        })

      this.newMessage = null

      try{
        let data = (await MsgServ.sendMsg(payload)).data
        console.log(data)
      }catch(e){
        console.log(e)
      }
    },

    async getMessages(){
      let payload = {
        token: localStorage.getItem('token'),
        otherUsername: this.otherUsername
      }

      try{
        let data = (await MsgServ.getMessages(payload)).data
        console.log(data)

        this.messages = data.chatMessages
        this.scrollToBottom('chat-msgs')
      }catch(e){
        console.log(e)
      }
    },

    call(){
      this.$socket.client.emit('call-request', {
        sender: this.$store.state.username,
        reciver: this.otherUsername
      })
    },

    onLoad(){
      this.otherUsername = this.$route.params.username
      this.getMessages()
    },
    onKeyPress(e) {
      if(e.key === 'Enter'){
        this.sendMessage()
      }
    },

    scrollToBottom(ref){
      setTimeout(() => {
        this.$refs[ref].$el.scrollTop = this.$refs[ref].$el.scrollHeight
      }, 50)
      
    }
  },
  
  sockets: {
    newMessage: async function(msg) {
      console.log('dobijena poruka preko socketa: ', msg)
      this.messages.push(msg)
      this.scrollToBottom('chat-msgs')
    },

    callDenied: async function(sender) {
      console.log('Poziv odbijen od ', sender)

      //TODO
    },

    callAccept: async function(sender) {
      console.log('Poziv prihvaen od ', sender)

      this.$store.dispatch('setPrivateCall', {callee: sender, caller: true})
    }
  },

  created: function() {
    this.onLoad()

    document.addEventListener('keydown', this.onKeyPress)
  },

  //on route change
  watch:{
    $route (to, from){
      console.log('In chat',to,from)
      if(to.name === from.name){
        this.onLoad()
      }
    }
  },

  destroyed: function() {
    document.removeEventListener('keydown', this.onKeyPress)
  },



  components: {
    ChatMsgs
  }
}
</script>

<style scoped>
.height100 {
  height: 100%;
}

.message-sender {
  position: absolute;
  bottom: 0px;
}
</style>